<template>
	<view class="page">
		<view class="head">
			<view class="uni-text-1">充值享好礼</view>
			<view class="uni-text-2">好礼多多 优惠多多</view>
		</view>
		<view class="list">
			<view class="uni-flex" v-for="(item,index) in MoneyList" :key="index" :class="index==CheakIndex?'checked':''">
				<view class="uni-flex-item-4" @tap="ItemClick(index)">
					<view class="uni-text-3"><text>{{item}}</text>元充值</view>
				</view>
			</view>
		</view>
		<view class="fixed">
			<view class="btn" @click="TopUp">立即充值</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				CheakIndex : 0,
				MoneyList : [50,100,200,500]
			}
		},
		methods: {
			ItemClick(index){
				let _self = this
				_self.CheakIndex = index
			},
			TopUp(){
				let _self = this;
				if(_self.CheakIndex>=_self.MoneyList.length || _self.CheakIndex<0) _self.CheakIndex = 0;
				var _money = _self.MoneyList[_self.CheakIndex]
				uni.navigateTo({
					url:'topupConfirm?value='+_money
				})
			}
		},
		onLoad(){
			
		},
	}
</script>

<style>
	.head{
		margin: 80upx 40upx 40upx 40upx;width: 670upx;height: 120upx;
		background-repeat:no-repeat; background-size:100% 100%;-moz-background-size:100% 100%;
		background-image: url("data:image/png;base64,");
	}
	.list{padding: 40upx;}
	.uni-flex{border: 1upx solid #e6e6e6;margin-bottom: 30upx;border-radius: 20upx;overflow: hidden;padding:40upx;}
	.uni-text-1{font-size:50upx;color: #333;}
	.uni-text-2{font-size:28upx;color: #666;}
	.uni-text-3{font-size:28upx;color: #333;line-height: 1;}
	.uni-text-3 text{font-size:70upx;font-weight: bold;margin-right: 10upx;}
	.uni-text-4{font-size:32upx;color: #666;line-height: 37upx;margin-top: 20upx;}
	.uni-text-4 image{width: 50upx;height: 37upx;margin: 0 10upx;margin-bottom: -6upx;}
	.uni-flex-item-1{text-align: center;}
	.uni-flex-item-1 image{width: 60upx;height: 60upx;margin: 27upx auto;}
	.checked {border-color: #3399FE;background-color: #fffbeb;}
	.checked .uni-text-3{color: #ff5858;}
	.fixed{position: fixed;left: 0;right: 0;bottom: 0;height: 180upx;}
	.fixed .btn{height: 100upx;width: 670upx;margin: 40upx;border-radius: 50upx;background: linear-gradient(120deg, #3399FE 0%, #3399FE 100%);color: #333;font-size: 36upx;line-height: 100upx;box-shadow: 0 0 20upx #ccc;text-align: center;}
</style>
